html { font-size: 16px; }

body { line-height: 1.5; word-wrap: break-word; }

a, .link { color: #004b66; text-decoration: none; }

a:hover, .link:hover, a:focus, .link:focus { color: smart-scale(#ffb499, 25%); text-decoration: none; outline: 0; }

h1, h2, h3, h4, h5, h6 { color: #444; font-family: Arial, Helvetica, Verdana, sans-serif, "Malgun Gothic"; margin-top: 1.5rem; margin-bottom: 1rem; font-weight: 600; }

body, select, input, textarea { color: #444; font-family: Arial, Helvetica, Verdana, sans-serif, "Malgun Gothic"; }

code, pre { display: block; font-family: source-code-pro, Consolas, Liberation Mono, Menlo, Courier New, Courier, mono; font-size: 0.85rem; line-height: 1.45; }

pre { margin: 1em 0; }

@custom-media --mini     (width<480px);
@custom-media --small    (width>=480px);
@custom-media --medium   (width>=768px);
@custom-media --large    (width>=960px);
@custom-media --xlarge   (width>=1220px);
@custom-media --small-down  (width<768px);
@custom-media --small-only  (480px <= width < 768px);
@custom-media --medium-only (768px <= width <960px);
@custom-media --large-only  (960px <=width < 1220px);
:root { --l-sidebar-width: 18rem; }

[id^='l-'] { box-sizing: border-box; }

#l-site-container { margin: 0; padding: 0; }

#l-header { position: relative; z-index: 1100; margin: 0 auto; @util clearfix; }

#l-header + .l-header-trailer { position: absolute; z-index: 1099; width: 100%; padding: 1rem; display: none; }

#l-content-wrapper { max-width: 1220px; margin: 0 auto; padding: 0.5rem; @util clearfix; }

#l-content { width: 100%; padding: 0.5rem 0.5rem; }

@media (--medium) { #l-content { float: right; width: calc(100% - var(--l-sidebar-width)); padding: 1rem 0.5rem 1rem 1rem; } }

#l-content.single { width: 100%; max-width: 960px; @util center; }

#l-sidebar { width: 100%; padding: 1rem 0.5rem; }

@media (--medium) { #l-sidebar { float: left; width: var(--l-sidebar-width); padding: 1rem 1rem 1rem 0.5rem; } }

#l-content.single + #l-sidebar { width: 100%; max-width: 960px; }

#l-footer { width: 100%; margin: 1rem auto; padding: 2rem 1rem; text-align: center; border-top: 1px solid gray(70%, 0.5); }

#l-offcanvas { z-index: 1200; }

#l-content pre.highlight { padding: 0.8rem; margin: 0.5rem 0; border-radius: 4px; background-color: #f3f6fa; border: solid 1px #dce6f0; }

/** Pygments styles: http://jwarby.github.io/jekyll-pygments-themes/languages/javascript.html# */
.highlight .hll { background-color: #ffffcc; }

.highlight .c { color: #0099FF; font-style: italic; }

/* Comment */
.highlight .err { color: #AA0000; background-color: #FFAAAA; }

/* Error */
.highlight .k { color: #006699; font-weight: bold; }

/* Keyword */
.highlight .o { color: #555555; }

/* Operator */
.highlight .cm { color: #0099FF; font-style: italic; }

/* Comment.Multiline */
.highlight .cp { color: #009999; }

/* Comment.Preproc */
.highlight .c1 { color: #0099FF; font-style: italic; }

/* Comment.Single */
.highlight .cs { color: #0099FF; font-weight: bold; font-style: italic; }

/* Comment.Special */
.highlight .gd { background-color: #FFCCCC; border: 1px solid #CC0000; }

/* Generic.Deleted */
.highlight .ge { font-style: italic; }

/* Generic.Emph */
.highlight .gr { color: #FF0000; }

/* Generic.Error */
.highlight .gh { color: #003300; font-weight: bold; }

/* Generic.Heading */
.highlight .gi { background-color: #CCFFCC; border: 1px solid #00CC00; }

/* Generic.Inserted */
.highlight .go { color: #AAAAAA; }

/* Generic.Output */
.highlight .gp { color: #000099; font-weight: bold; }

/* Generic.Prompt */
.highlight .gs { font-weight: bold; }

/* Generic.Strong */
.highlight .gu { color: #003300; font-weight: bold; }

/* Generic.Subheading */
.highlight .gt { color: #99CC66; }

/* Generic.Traceback */
.highlight .kc { color: #006699; font-weight: bold; }

/* Keyword.Constant */
.highlight .kd { color: #006699; font-weight: bold; }

/* Keyword.Declaration */
.highlight .kn { color: #006699; font-weight: bold; }

/* Keyword.Namespace */
.highlight .kp { color: #006699; }

/* Keyword.Pseudo */
.highlight .kr { color: #006699; font-weight: bold; }

/* Keyword.Reserved */
.highlight .kt { color: #007788; font-weight: bold; }

/* Keyword.Type */
.highlight .m { color: #FF6600; }

/* Literal.Number */
.highlight .s { color: #CC3300; }

/* Literal.String */
.highlight .na { color: #330099; }

/* Name.Attribute */
.highlight .nb { color: #336666; }

/* Name.Builtin */
.highlight .nc { color: #00AA88; font-weight: bold; }

/* Name.Class */
.highlight .no { color: #336600; }

/* Name.Constant */
.highlight .nd { color: #9999FF; }

/* Name.Decorator */
.highlight .ni { color: #999999; font-weight: bold; }

/* Name.Entity */
.highlight .ne { color: #CC0000; font-weight: bold; }

/* Name.Exception */
.highlight .nf { color: #CC00FF; }

/* Name.Function */
.highlight .nl { color: #9999FF; }

/* Name.Label */
.highlight .nn { color: #00CCFF; font-weight: bold; }

/* Name.Namespace */
.highlight .nt { color: #330099; font-weight: bold; }

/* Name.Tag */
.highlight .nv { color: #003333; }

/* Name.Variable */
.highlight .ow { color: #000000; font-weight: bold; }

/* Operator.Word */
.highlight .w { color: #bbbbbb; }

/* Text.Whitespace */
.highlight .mf { color: #FF6600; }

/* Literal.Number.Float */
.highlight .mh { color: #FF6600; }

/* Literal.Number.Hex */
.highlight .mi { color: #FF6600; }

/* Literal.Number.Integer */
.highlight .mo { color: #FF6600; }

/* Literal.Number.Oct */
.highlight .sb { color: #CC3300; }

/* Literal.String.Backtick */
.highlight .sc { color: #CC3300; }

/* Literal.String.Char */
.highlight .sd { color: #CC3300; font-style: italic; }

/* Literal.String.Doc */
.highlight .s2 { color: #CC3300; }

/* Literal.String.Double */
.highlight .se { color: #CC3300; font-weight: bold; }

/* Literal.String.Escape */
.highlight .sh { color: #CC3300; }

/* Literal.String.Heredoc */
.highlight .si { color: #AA0000; }

/* Literal.String.Interpol */
.highlight .sx { color: #CC3300; }

/* Literal.String.Other */
.highlight .sr { color: #33AAAA; }

/* Literal.String.Regex */
.highlight .s1 { color: #CC3300; }

/* Literal.String.Single */
.highlight .ss { color: #FFCC33; }

/* Literal.String.Symbol */
.highlight .bp { color: #336666; }

/* Name.Builtin.Pseudo */
.highlight .vc { color: #003333; }

/* Name.Variable.Class */
.highlight .vg { color: #003333; }

/* Name.Variable.Global */
.highlight .vi { color: #003333; }

/* Name.Variable.Instance */
.highlight .il { color: #FF6600; }

