#l-header .nav { width: 100%; float: right; }

#l-header .nav .logo { font-size: 1.8rem; font-weight: 500; float: left; margin: 0; }

#l-header .nav .logo a { color: white; text-decoration: none; }

#l-header .nav .logo a:before { content: ''; display: inline-block; position: relative; width: 1.5rem; height: 1.5rem; background: svg-load("gbm.svg", fill=#fff) no-repeat; background-size: contain; top: 0.4rem; margin-right: 0.5rem; }

#l-header .nav .ui.menu { float: right; margin: 0; box-shadow: none; background: transparent; border: none; }

#l-header .nav .ui.menu .item { font-size: 1.8rem; padding: 0.5rem; color: gray(80%); }

#l-header .nav .ui.menu .item:before { background: transparent; }

#l-header .nav .ui.menu .item:hover { color: white; }

#l-header { position: relative; padding: 0.5rem 1rem; color: #fff; text-align: center; height: 4rem; background-color: #004b66; background-image: linear-gradient(45deg, #00384D, #004b66); }

#l-banner { margin: 0; padding: 2rem 1rem; color: gray(90%); text-align: center; background-color: #004d66; }

#l-banner .title { font-size: 2rem; color: white; }

#l-banner .description { font-size: 1.2rem; font-weight: normal; padding: 0; margin-bottom: 0.5rem; }

#l-banner .version { margin-bottom: 0.5rem; padding: 0.5rem; }

#l-banner .btn, #l-banner .btn:visited { color: rgba(255, 255, 255, 0.7); }

#l-banner .btn { display: inline-block; text-decoration: none; margin-bottom: 1rem; background-color: rgba(255, 255, 255, 0.08); border: 1px solid rgba(255, 255, 255, 0.2); border-radius: 0.3rem; transition: color 0.2s, background-color 0.2s, border-color 0.2s; padding: 0.6rem 0.9rem; font-size: 0.9rem; }

#l-banner .btn:visited { text-decoration: none; }

#l-sidebar { font-size: 0.95rem; }

@media (--medium) { #l-sidebar { position: sticky; top: 0; } }

#l-sidebar > .title { text-align: center; }

#l-sidebar > ul { margin: 0; padding: 0; list-style: none; }

#l-sidebar > ul a:hover { color: #790d0d; }

#l-sidebar > ul ul { margin: 0; list-style-type: disc; padding: 0 1rem 0.5rem 1rem; }

#l-sidebar > ul > li { margin: 0.5rem 0 0 0; padding: 0.35rem 1rem; border-radius: 3px; background: color(#004b66 a(5%)); }

#l-content { padding-top: 1rem; border-top: 1px solid gray(60%, 0.3); }

@media (--medium) { #l-content { border-top: none; } }

#l-content em { color: #c30; }

#l-content h1, #l-content h2, #l-content h3, #l-content h4, #l-content h5, #l-content h6 { color: #004b66; }

#l-content h4, #l-content h5, #l-content h6 { margin-bottom: 0.1rem; }
